@import url(https://fonts.googleapis.com/css2?family=Electrolize&family=Fira+Code:wght@300;400;500;600;700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
@charset 'utf-8';

:root {
	--color-black: #000;
	--color-blue: #3F84E5;
	--color-blueDarken: #4361EE;
	--color-gray: #909090;
	--color-grayDark: #2D2D2D;
	--color-grayDarken: #1a1a1b;
	--color-highlight-green: #04d361;
	--color-highlight-greenDarken: #03b955;
	--color-highlight-greenDarkness: #03a14a;
	--color-highlight-yellow: #ffef5c;
	--color-highlight-red: #ff78a0;
	--color-primary: #7209b7;
	--color-secondary: #f0f0f7;
	--color-purpleDarken: #3a0ca3;
	--color-logo: #bf60ff;
	--color-red: #FF4B4B;
	--color-redDarken: #FF3232;
	--color-redDarkness: #df2727;
	--color-white: #fff;
	
	font-size: 60%;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;

	scrollbar-color: #909090 #1a1a1b;

	scrollbar-color: var(--color-gray) var(--color-grayDarken);
	scroll-behavior: smooth;
}

html,
body,
#root {
	height: 100vh;

	cursor: default;
}

body {
	background: #1a1a1b;
	background: var(--color-grayDarken);
	--bodyPaddingTop: 10vh;
}

#root {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	min-height: calc(100vh - var(--bodyPaddingTop));
}

body,
input,
button,
textarea {
	font: 500 1.6rem Roboto;
}

.container {
	width: 100%;
	max-width: 100vw;
	height: 100vh;
}

@media (min-width: 700px) {
	:root {
		font-size: 62.5%;
	}
}

main {
	width: 100%;
	max-width: 100%;

	-webkit-scroll-snap-type: y mandatory;

	    -ms-scroll-snap-type: y mandatory;

	        scroll-snap-type: y mandatory;
}

header {
	position: fixed;
	width: 100%;
	height: 10vh;
	left: 0px;
	top: 0px;
	z-index: 150;

	padding: 0 5%;

	display: flex;
	justify-content: space-between;
	align-items: center;

	color: var(--color-white);

	background: var(--color-black);
	border-bottom: 2px solid var(--color-primary);
	box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 3px 1px;
}

header .logo {
	position: relative;
	font-family: 'Electrolize', sans-serif;
	font-size: 2.6rem;
	font-weight: 500;
	font-style: italic;

	cursor: pointer;
}

header .logo b {
	color: var(--color-logo);
}

header .logo img {
	position: absolute;
	top: 2px;
	width: 30px;

	transform: rotate(5deg);
}

header nav ul {
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-direction: row;

	list-style: none;

	font-family: 'Roboto', sans-serif;
}

header nav ul li {
	letter-spacing: 2px;

	margin-left: 10px;
}

header nav ul li:not(:last-child) {
	margin-right: 10px;
}

header nav ul li a {
	position: relative;

	text-decoration: none;
	cursor: pointer;

	display: flex;
	align-items: center;
	justify-content: center;

	font-size: 1.6rem;
}

header nav ul li a:after {
	content: '';
	position: absolute;
	left: 50%;
	bottom: -10px;
	transform: translateX(-50%) scaleX(0);
	transform-origin: 50% 50%;
	width: 100%;
	height: 4px;
	background: var(--color-purpleDarken);
	transition: all 100ms;
	opacity: 0;
	border-radius: 5px;
}

header nav ul li a:hover:after {
	transform: translateX(-50%) scaleX(1);
	opacity: 1;
}

header nav ul li > .active:after {
	transform: translateX(-50%) scaleX(1);
	opacity: 1;

	background: var(--color-primary);
}

header .MuiSvgIcon-root {
	font-size: 1.6em;
}

header nav ul li a .MuiSvgIcon-root {
	margin-right: 3px;
}

header .menu-icon {
	display: none;
}

header .menu-icon {
	padding: 1rem;
	border-radius: 50%;

	cursor: pointer;

	transition: background-color .3s;
}

header .menu-icon:hover {
	background: var(--color-grayDarken);
}

header .menu-icon:active {
	background: var(--color-gray);
}

.menu-toggle {
	display: flex;

	height: 90vh;

	visibility: visible;
	opacity: 1;
}

.block-overflow {
	overflow-y: hidden;
}

/* MOBILE */

@media (max-width: 1024px) and (max-height: 1366px) {
	header nav ul {
		position: absolute;
		left: 0;
		right: 0;
		top: var(--bodyPaddingTop);
		bottom: 0;

		z-index: -1;

		flex-direction: column;
		justify-content: center;

		visibility: hidden;
		opacity: 0;

		width: 100%;
		height: 75vh;

		background: linear-gradient(to bottom, var(--color-black) 10%, rgba(0, 0, 0, 0.9) 50%, var(--color-black) 90%);

		transition: all .3s ease-in-out;
	}

	header nav ul li {
		margin: 2.6rem 0 !important;
	}

	header nav ul li a {
		font-size: 2.6rem;
	}

	header .github-icon {
		display: none;
	}

	header .menu-icon {
		display: flex;
		align-items: center;
	}
}

@media (max-width: 900px) and (max-height: 500px) and (orientation: landscape) {
	header nav ul {
		position: absolute;
		left: 0;
		right: 0;
		top: var(--bodyPaddingTop);
		bottom: 0;

		z-index: -1;

		flex-direction: column;
		justify-content: center;

		visibility: hidden;
		opacity: 0;

		width: 100%;
		height: 75vh;

		background: linear-gradient(to bottom, var(--color-black) 10%, rgba(0, 0, 0, 0.9) 50%, var(--color-black) 90%);

		transition: all .3s ease-in-out;
	}

	header nav ul li {
		margin: 0.6rem 0 !important;
	}

	header nav ul li a {
		font-size: 2.6rem;
	}

	header .github-icon {
		display: none;
	}

	header .menu-icon {
		display: flex;
		align-items: center;

		padding: 0.3rem;
	}
}

/* APARELHOS MENORES */

@media (max-width: 325px) and (max-height: 700px) and (orientation: portrait) {
	header nav ul li {
		margin: 2rem 0 !important;
	}
}

