.scroll-down {
	width: 100%;
	height: 100%;

	display: flex;
	align-items: center;
  justify-content: flex-end;
	flex-direction: column;
	
	color: var(--color-gray);
	transition: color .3s;

	margin-bottom: 5px;
}

.scroll-down .arrow-content {
	display: flex;
	align-items: center;
	flex-direction: column;

	cursor: pointer;
}

.scroll-down .arrow-content span {
	font-weight: 300;
	text-align: center;

  position: absolute;
	margin-bottom: 75px;
	transition: color .3s;
	font-size: 1.6rem;

	text-shadow: 0px 4px 10px rgba(0, 0, 0, 1);
}

.scroll-down .arrow-content .arrow-down {
	font-size: 94px;

	-webkit-animation: pulse 2s infinite;

	        animation: pulse 2s infinite;
	transition: color .3s;

	-webkit-filter: drop-shadow(0px 4px 10px rgba(0, 0, 0, 1));

	        filter: drop-shadow(0px 4px 10px rgba(0, 0, 0, 1));
}

.scroll-down .arrow-content:hover span {
	color: var(--color-secondary);
}

.scroll-down .arrow-content:hover .arrow-down {
	color: #944dd3;
}

@-webkit-keyframes pulse {
	0% {
		transform: translate(0, 0);
	}
	50% {
		transform: translate(0, 10px);
	}
	100% {
		transform: translate(0, 0);
	}
}

@keyframes pulse {
	0% {
		transform: translate(0, 0);
	}
	50% {
		transform: translate(0, 10px);
	}
	100% {
		transform: translate(0, 0);
	}
}

@media (max-width: 1200px) and (max-height: 800px) and (orientation: landscape) {
	.scroll-down .arrow-content span {
		font-size: 1.3rem;
	}
	.scroll-down .arrow-content .arrow-down {
		font-size: 64px;
	}
}

